body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    width: 100%;
    height: 100vh;
    padding-top: 10px;
    background-color: #1b1b32;
    font-family: Lato, sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    color: white;
}

.container-calc {
    width: 400px;
    height: 600px;
    padding: 14px;
    background-color: #99c9ff;
    border-radius: 20px;
    border: 5px solid white;
}

.file {
    display: flex;
    margin: 4px;
    align-items: center;
    justify-content: center;
}
.container-boton {
    height: 83px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: 38px;
    background: #1b1b32;
    color: white;
    border-radius: 25px;
    border: 2px solid white;
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    flex: 1 1;
}

.operator {
    background-color: #00471b;
}

.container-boton:hover {
    background-color: #5a01a7;
}

.operator:hover {
    background-color: #ff6600;
}
.input {
height: 75px;
border-radius: 50px;
margin-bottom: 20px;
display: flex;
justify-content: flex-end;
align-items: center;
font-size: 30px;
background-color: #0a0a23;
color: white;
padding: 11px 30px 11px 11px;
margin: 1px solid #888;
box-shadow: -1px 4px 1px 1px white;

}
.clear-button {
height: 80px;
font-size: 1.6em;
display: flex;
flex: 1 1;
background-color: #ac0246;
margin-top: 8px;
justify-content: center;
align-items: center;
font-weight: bold;
color: white;
border: 2px solid white;
cursor: pointer;
border-radius: 40px;
}

.clear-button:hover {
    background-color: #ebb000;
}
